import React from 'react';
import {
    Wrapper,
    Header,
    Title,
    TextBody,
    Ol,
    Li,
    PoliticsDecoration,
    BiancoLogo,
    LogoContainer,
} from './Politics.elemets';


class Politics extends React.Component{
    constructor(props){
        super(props)
    };

    render(){
        return(
            <>
                <Wrapper>
                    <Header>
                        <Title active>POLÍTICAS</Title>
                        <Title>MISIÓN Y VISIÓN</Title>
                        <Title>RECURSOS HUMANOS</Title>
                    </Header>

                    <TextBody>
                        <p>
                            Somos un equipo de trabajo cuyas acciones diarias las ejecutamos con una elevada vocación de servicio a
                            los Clientes en nuestra visión de empresa de categoría mundial, basadas en los siguientes principios:
                        </p>
                        <Ol>
                            <Li>INTEGRIDAD PERSONAL como expresión de disciplina, orden, respeto, honestidad y entusiasmo.</Li>
                            <Li>CREATIVIDAD E INNOVACIÓN como parte de nuestro reto diario para el mejoramiento continúo.</Li>
                            <Li>PRODUCTIVIDAD en nuestro trabajo y en el empleo de los recursos materiales.</Li>
                            <Li>
                                CONSCIENCIA en la práctica de un trabajo libre de errores y en el COMPROMISO leal con la 
                                institución y con las realizaciones de calidad.
                            </Li>
                            
                        </Ol>
                        
                        <PoliticsDecoration/>
                        
                    </TextBody>
                    <LogoContainer>
                        <BiancoLogo/>
                    </LogoContainer>
                    

                </Wrapper>

            </>
        )
    }
}

export default Politics;